import Entity from './Entity.js';
import Sprite from '../enums/Sprite.js';

export default class Bullet extends Entity {
    constructor(x, y) {
        super(x, y);

        this.sprite = Sprite.MISSILE;
        this.fuel = 3;
        this.life = 10;
    }

    update() {
        super.update();

        this.life -= 0.1;
        if (this.fuel > 0) {
            this.fuel -= 0.1;
            this.accelerate(0.5);
        }
        if (this.life <= 0) {
            this.alive = false;
        }
    }
}